import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { Router } from '@angular/router';
import {
  PlaylistPreviewTracksModel,
  PlaylistPreviewInnerItemModel,
} from '../../store/playlist-preview/model';
import { faPlay, faTimes } from '@fortawesome/free-solid-svg-icons';
import { SwalFire, Toast } from '../../services/index';
import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';
import { SubSink } from 'subsink';
import { Store } from '@ngxs/store';
import { StateReset } from 'ngxs-reset-plugin';
import { PlaylistPreviewState } from '../../store/playlist-preview/state';
import { PlaylistPreviewActionsType } from '../../store/playlist-preview/actions.type';

@Component({
  selector: 'app-playlist-track',
  templateUrl: './playlist-track.component.html',
  styleUrls: ['./playlist-track.component.scss'],
})
export class PlaylistTrackComponent implements OnInit, OnDestroy {
  @Input() tracks: PlaylistPreviewTracksModel;
  @Input() config: any;
  @Input() default: boolean = false;
  @Input() isMyPlaylist: boolean;
  @Input() playlistId: string;

  faPlay = faPlay;
  faTimes = faTimes;

  toast = Toast();
  swalFire = SwalFire;
  req$ = new SubSink();

  @Dispatch() playlistPreview = (playlistId: string) =>
    new PlaylistPreviewActionsType.FetchPlaylistPreview(playlistId);

  constructor(
    private route: Router,
    private http: MyPlaylistService,
    private store: Store
  ) {}

  ngOnInit(): void {}

  pageChanged(event) {
    this.config.currentPage = event;
  }

  trackPreview(track) {
    localStorage.removeItem('selected-audio-feature');
    localStorage.setItem(
      'selected-audio-feature',
      JSON.stringify({
        image: track.album.images[0].url,
        name: track.name,
        artists: track.artists,
        description: track.album.name,
        popularity: track.popularity,
        id: track.id,
        external_urls: track.external_urls,
      })
    );

    this.route.navigate(['audio-feature', track.id]);
  }

  removeTrackItem(item: PlaylistPreviewInnerItemModel) {
    const img = item.track.album.images;
    const options = {
      title: item.track.name,
      text: 'Are you sure to remove this item from this playlist',
      imageUrl: img.length > 0 ? img[0].url : 'assets/images/spotify.png',
      confirmButtonText: 'Remove Track',
    };

    this.swalFire((result) => {
      if (result.value) {
        this.req$.sink = this.http
          .removePlaylistItem(this.playlistId, {
            tracks: [{ uri: item.track.uri }],
          })
          .subscribe(
            (res) => {
              this.toast.fire({
                icon: 'success',
                title: 'Item has been remove from this playlist',
              });
              this.store.dispatch(new StateReset(PlaylistPreviewState));
              this.playlistPreview(this.playlistId);
            },
            (err) => {
              this.toast.fire({
                icon: 'error',
                title: 'Failed request remove item ',
              });
              console.log(err);
            }
          );
      }
    }, options);
  }

  ngOnDestroy() {
    this.req$.unsubscribe();
  }
}
