import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { FollowService } from '../../services/follow/follow.service';
import { FollowActionsType } from './actions.type';
import { FollowArtistModel } from './artists-model';

import { AuthService } from '../../auth/auth.service';

@State<FollowArtistModel>({
  name: 'follow_artists',
  defaults: null,
})
@Injectable()
export class FollowArtistsState {
  constructor(private service: FollowService, private auth: AuthService) {}

  @Action(FollowActionsType.FetchFollowArtists)
  fetchFollowArtists(
    { getState, patchState }: StateContext<FollowArtistModel>,
    { queryParams }: FollowActionsType.FetchFollowArtists
  ) {
    const state$ = getState();

    if (state$ == null) {
      return this.service.fetchFollowArtists(queryParams).pipe(
        map((result: any) => {
          patchState(result);
        }),
        catchError((error: any) => {
          console.log('top-tracks', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
