import { Component, OnInit, OnDestroy } from '@angular/core';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { ActivatedRoute, Router } from '@angular/router';
import { StateReset } from 'ngxs-reset-plugin';

import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { SubSink } from 'subsink';

import { PlaylistPreviewActionsType } from '../../store/playlist-preview/actions.type';
import { PlaylistPreviewSelectors } from '../../store/playlist-preview/selectors';
import { PlaylistPreviewModel } from '../../store/playlist-preview/model';
// import { TopTrackItemModel } from '../../store/top-tracks/model';
import { MyPlaylistSelectors } from '../../store/my-playlist/selectors';
import { SpotifyModel } from '../../store';

import { CurrentUserSelectors } from '../../store/current-user/selectors';
import { CurrentUserModel } from '../../store/current-user/model';

import { RecommendationState } from '../../store/recommendation/state';

@Component({
  selector: 'app-playlist-preview',
  templateUrl: './playlist-preview.component.html',
  styleUrls: ['./playlist-preview.component.scss'],
})
export class PlaylistPreviewComponent implements OnInit, OnDestroy {
  @Dispatch() playlistPreview = (playlistId: string) =>
    new PlaylistPreviewActionsType.FetchPlaylistPreview(playlistId);

  @Select(PlaylistPreviewSelectors.fetchPlaylistPreview)
  playlist$: Observable<PlaylistPreviewModel>;

  @Select(MyPlaylistSelectors.fetchPlaylist)
  myPlaylist$: Observable<SpotifyModel>;

  @Select(CurrentUserSelectors.fetchCurrentUser)
  currentUser$: Observable<CurrentUserModel>;

  req$ = new SubSink();
  config: any;
  isEditBtn = false;

  constructor(
    private activeRoute: ActivatedRoute,
    private route: Router,
    private store: Store
  ) {}

  ngOnInit(): void {
    const playlistId = this.activeRoute.snapshot.params.playlistId;
    this.playlistPreview(playlistId);

    this.req$.sink = this.playlist$.subscribe(
      (playlist: PlaylistPreviewModel) => {
        if (playlist) {
          this.config = {
            itemsPerPage: 20,
            currentPage: 1,
            totalItems: playlist.tracks.items.length,
          };
        }
      }
    );

    this.store.dispatch(new StateReset(RecommendationState));
  }

  pageChanged(event) {
    this.config.currentPage = event;
  }

  closeEditButton(e) {
    this.isEditBtn = false;
  }

  getRecommendation(playlistId) {
    localStorage.removeItem('recommendation');
    this.route.navigate(['my-playlist', playlistId, 'recommendation']);
  }

  ngOnDestroy() {
    this.req$.unsubscribe();
  }
}
