import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

// import { throwError } from 'rxjs';
// import { map, catchError } from 'rxjs/operators';

import { InternalActionsType } from './actions.type';
import { InternalModel } from './model';

// import { AuthService } from '../../auth/auth.service';

@State<InternalModel>({
  name: 'internal',
  defaults: null,
})
@Injectable()
export class InternalState {
  constructor() {}

  @Action(InternalActionsType.setInternal)
  setInternal(
    { getState, patchState }: StateContext<InternalModel>,
    { params }: InternalActionsType.setInternal
  ) {
    const state$ = getState();

    patchState({ ...state$, ...params });
  }
}
