import { Component, OnInit } from '@angular/core';

import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';

import { TopArtistsActionsType } from '../../store/top-artists/actions.type';
import { TopArtistsSelectors } from '../../store/top-artists/selectors';
import { SpotifyModel } from '../../store/';
import { tabs } from './top-artists-util';

@Component({
  selector: 'app-top-artists',
  templateUrl: './top-artists.component.html',
  styleUrls: ['./top-artists.component.scss'],
})
export class TopArtistsComponent implements OnInit {
  constructor(private store: Store) {}

  $tabs = tabs;
  @Dispatch() fetchTopArtists = (qry = null) =>
    new TopArtistsActionsType.FetchTopArtists(qry);

  @Select(TopArtistsSelectors.fetchTopArtists) topArtists$: Observable<
    SpotifyModel
  >;

  ngOnInit(): void {
    const { top_artists } = this.store.snapshot();
    if (top_artists === null) {
      const local = JSON.parse(localStorage.getItem('top-artists'));
      if (!local || local.time_range === 'undefined') {
        this.fetchTopArtists({ limit: 20, time_range: 'long_term' });
        this.setActiveTab('long_term');
      } else {
        this.fetchTopArtists({ limit: 20, time_range: local.time_range });
        this.setActiveTab(local.time_range);
      }
    }
  }

  timeRange(time_range) {
    this.fetchTopArtists({ limit: 20, time_range });
    this.setActiveTab(time_range);
  }

  setActiveTab(time_range: string) {
    this.$tabs.map((tab) => {
      tab.status = tab.timeRange === time_range ? 'is-active' : '';
      return tab;
    });
  }
}
