import { QueryParams } from '../store';
import Swal from 'sweetalert2';

export const httpOptions = {
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
    Authorization: `Bearer ${localStorage.getItem('access_token')}`,
  },
};

export const setQueryParams = (queryParams: QueryParams = null) => {
  const $query = [];
  if (queryParams !== null) {
    for (const [key, value] of Object.entries(queryParams)) {
      $query.push(`${key}=${value}`.replace(/,/g, '__'));
    }

    // if (queryParams.limit) $query.push(`limit=${queryParams.limit}`);
    // if (queryParams.offset) $query.push(`offset=${queryParams.offset}`);
    // if (queryParams.time_range)
    // $query.push(`time_range=${queryParams.time_range}`);
  }

  return $query.toString().replace(/,/g, '&').replace(/__/g, ',');
};

export const msToTime = (s) => {
  let ms = s % 1000;
  s = (s - ms) / 1000;
  let secs = s % 60;
  s = (s - secs) / 60;
  let mins = s % 60;
  let hrs = (s - mins) / 60;

  // return hrs + ':' + mins + ':' + secs + '.' + ms;
  return (mins < 10 ? '0' + mins : mins) + ':' + secs;
};

export const Toast = () =>
  Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    onOpen: (toast) => {
      toast.addEventListener('mouseenter', Swal.stopTimer);
      toast.addEventListener('mouseleave', Swal.resumeTimer);
    },
  });

export const SwalFire = (callback, options) => {
  Swal.fire({
    ...options,
    imageWidth: 200,
    imageHeight: 200,
    confirmButtonColor: '#1db954',
    cancelButtonColor: '#222a2e',
    showCancelButton: true,
  }).then(callback);
};
