import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { AudioFeatureService } from '../../services/audio-feature/audio-feature.service';
import { AudioFeatureActionsType } from './actions.type';
import { AudioFeatureModel } from './model';

import { AuthService } from '../../auth/auth.service';

@State<AudioFeatureModel>({
  name: 'audio_feature',
  defaults: null,
})
@Injectable()
export class AudioFeatureState {
  constructor(
    private service: AudioFeatureService,
    private auth: AuthService
  ) {}

  @Action(AudioFeatureActionsType.FetchAudioFeature)
  fetchAudioFeature(
    { getState, patchState }: StateContext<AudioFeatureModel>,
    { track }: AudioFeatureActionsType.FetchAudioFeature
  ) {
    return this.service.fetchAudioFeature(track.id).pipe(
      map((result: any) => {
        patchState({
          ...track,
          feature: result,
        });
      }),
      catchError((error: any) => {
        console.log('current-user', error);
        // this.auth.authenticateUser();
        return throwError(error);
      })
    );
  }
}
