import { Injectable } from '@angular/core';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import * as queryString from 'query-string';
import { StateClear } from 'ngxs-reset-plugin';

import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  constructor(
    private http: HttpClient,
    private router: Router,
    private store: Store
  ) {}

  /**
   * isAuthenticated
   */
  public isAuthenticated() {
    const token = localStorage.getItem('access_token');

    if (token == null || token === '' || token == 'undefined') return false;

    return true;
  }

  /**
   * authenticateUser
   */
  public authenticateUser() {
    const state$ = this.generateRandomString(16);

    localStorage.setItem('state', state$);

    const scope$ = [
      'user-read-private',
      'user-read-email',
      'user-follow-read',
      'user-top-read',
      'playlist-read-private',
      // modify user follower scope
      'user-follow-read',
      'user-follow-modify',
      // modify public and private playlist scope
      'playlist-modify-public',
      'playlist-modify-private',
    ];

    window.location.href =
      'https://accounts.spotify.com/authorize?' +
      queryString.stringify({
        response_type: 'code',
        client_id: environment.client_id,
        scope: scope$.toString().replace(/,/g, ' '),
        redirect_uri: environment.redirect_uri,
        state: state$,
      });

    this.store.dispatch(new StateClear());
  }

  /**
   * generateRandomString
   */
  public generateRandomString(length) {
    var text = '';
    var possible =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

    for (var i = 0; i < length; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
  }

  /**
   * authHttpHeaders
   */
  public authHttpHeaders() {
    return new HttpHeaders({
      Authorization: `Basic ${btoa(
        environment.client_id + ':' + environment.client_secret
      )}`,
      'Content-Type': 'application/x-www-form-urlencoded',
    });
  }

  /**
   * generateToken
   */
  public generateToken(code: string) {
    const headers = this.authHttpHeaders();

    const params = new HttpParams()
      .set('code', code)
      .set('redirect_uri', environment.redirect_uri)
      .set('grant_type', 'authorization_code');

    return this.http.post<any>(
      `${environment.spotify_api_token}`,
      params.toString(),
      { headers }
    );
  }

  /**
   * refreshToken
   */
  public refreshToken() {
    const refreshToken = localStorage.getItem('refresh_token');
    const headers = this.authHttpHeaders();
    const params = new HttpParams()
      .set('grant_type', 'refresh_token')
      .set('refresh_token', refreshToken);

    return this.http.post<any>(
      `${environment.spotify_api_token}`,
      params.toString(),
      { headers }
    );
  }

  /**
   * UsersProfile
   */
  public fetchCurrentUser() {
    return this.http.get<any[]>(`${environment.spotify_api}me`);
  }

  /**
   * accessTokenChecker
   */
  public accessTokenChecker() {
    const accessToken = localStorage.getItem('access_token');
    return accessToken || accessToken !== '' ? true : false;
  }

  /**
   * callback_times
   */
  public setCallbackTimes() {
    let times = localStorage.getItem('callback_times');
    let cons$ = times === null ? 1 : parseInt(times) + 1;
    localStorage.setItem('callback_item', cons$.toString());
  }
}
