import { Component, OnInit } from '@angular/core';

import { Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';

import { MyPlaylistActionsType } from '../../store/my-playlist/actions.type';
import { MyPlaylistSelectors } from '../../store/my-playlist/selectors';
import { SpotifyModel } from '../../store/';

@Component({
  selector: 'app-my-playlist',
  templateUrl: './my-playlist.component.html',
  styleUrls: ['./my-playlist.component.scss'],
})
export class MyPlaylistComponent implements OnInit {
  @Dispatch() fetchMyPlaylist = (qry = null) =>
    new MyPlaylistActionsType.FetchPlaylist(qry);

  @Select(MyPlaylistSelectors.fetchPlaylist) playlist$: Observable<
    SpotifyModel
  >;

  constructor() {}

  ngOnInit(): void {
    this.fetchMyPlaylist({
      limit: 50,
    });
  }
}
