import { Component, OnInit } from '@angular/core';

import { Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';

import { RelatedArtistActionsType } from '../../store/related-artists/actions.type';
import { RelatedArtistsSelectors } from '../../store/related-artists/selectors';
// import { TopArtistsModel } from '../../store/top-artists/model';
import { RelatedArtistsModel } from '../../store/related-artists/model';

import { ArtistPreviewActionsType } from '../../store/artist-preview/actions.type';
import { ArtistPreviewSelectors } from '../../store/artist-preview/selectors';
import { ArtistPreviewModel } from '../../store/artist-preview/model';

import { ArtistTopTracksActionsType } from '../../store/artist-top-tracks/actions.type';
import { ArtistTopTracksSelectors } from '../../store/artist-top-tracks/selectors';
import { ArtistTopTracksModel } from '../../store/artist-top-tracks/model';

import { FollowSelectors } from '../../store/follow/selectors';
import { FollowArtistModel } from '../../store/follow/artists-model';

import { Router, ActivatedRoute } from '@angular/router';
// import { FollowService } from '../../services/follow/follow.service';

@Component({
  selector: 'app-artist-preview',
  templateUrl: './artist-preview.component.html',
  styleUrls: ['./artist-preview.component.scss'],
})
export class ArtistPreviewComponent implements OnInit {
  constructor(private route: Router, private activeRoute: ActivatedRoute) {
    this.route.routeReuseStrategy.shouldReuseRoute = () => false;
  }

  @Dispatch() fetchArtistPreview = (artistId: string) =>
    new ArtistPreviewActionsType.FetchArtistPreview(artistId);

  @Dispatch() fetchRelatedArtists = (artistId: string) =>
    new RelatedArtistActionsType.FetchRelatedArtist(artistId);

  @Dispatch() fetchArtistTopTracks = (artistId: string) =>
    new ArtistTopTracksActionsType.FetchArtistTopTracks(artistId);

  @Select(ArtistPreviewSelectors.fetchArtistPreview)
  artistPreview$: Observable<ArtistPreviewModel>;

  @Select(RelatedArtistsSelectors.fetchTopArtists)
  relatedArtists$: Observable<RelatedArtistsModel>;

  @Select(ArtistTopTracksSelectors.fetchTopTracks)
  topTracks$: Observable<ArtistTopTracksModel>;

  @Select(FollowSelectors.fetchFollowArtists)
  followArtist$: Observable<FollowArtistModel>;

  ngOnInit(): void {
    const artistId = this.activeRoute.snapshot.paramMap.get('artistId');
    this.fetchArtistPreview(artistId);
    this.fetchRelatedArtists(artistId);
    this.fetchArtistTopTracks(artistId);
  }

  withComma(n) {
    return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  }

  previewArtistCallback(artistId) {
    this.route.navigate(['artist', artistId]);
    console.log(artistId);
  }
}
