import { Component, OnInit, OnDestroy } from '@angular/core';

import { Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import {
  faMicrophoneAlt,
  faMusic,
  faPlay,
} from '@fortawesome/free-solid-svg-icons';
import { SubSink } from 'subsink';

import { CurrentUserActionsType } from '../../store/current-user/actions.type';
import { CurrentUserSelectors } from '../../store/current-user/selectors';
import { CurrentUserModel } from '../../store/current-user/model';

import { MyPlaylistActionsType } from '../../store/my-playlist/actions.type';
import { MyPlaylistSelectors } from '../../store/my-playlist/selectors';
import { SpotifyModel } from '../../store/index';

import { FollowActionsType } from '../../store/follow/actions.type';
import { FollowSelectors } from '../../store/follow/selectors';
import { FollowArtistModel } from '../../store/follow/artists-model';

import { InternalActionsType } from '../../store/internal/actions.type';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
})
export class MainComponent implements OnInit, OnDestroy {
  faMicrophoneAlt = faMicrophoneAlt;
  faMusic = faMusic;
  faPlay = faPlay;

  userReq$ = new SubSink();

  @Dispatch() fetchCurrentUser = () =>
    new CurrentUserActionsType.FetchCurrentUser();

  @Dispatch() fetchMyPlaylist = (qry) =>
    new MyPlaylistActionsType.FetchPlaylist(qry);

  @Dispatch() fetchFollowArtists = () =>
    new FollowActionsType.FetchFollowArtists();

  @Dispatch() setInternal = (params) =>
    new InternalActionsType.setInternal(params);

  @Select(CurrentUserSelectors.fetchCurrentUser) user$: Observable<
    CurrentUserModel
  >;

  @Select(MyPlaylistSelectors.fetchPlaylist) playlist$: Observable<
    SpotifyModel
  >;

  @Select(FollowSelectors.fetchFollowArtists) artists$: Observable<
    FollowArtistModel
  >;

  constructor() {}

  ngOnInit(): void {
    localStorage.removeItem('callback_times');
    this.fetchCurrentUser();

    this.userReq$.sink = this.user$.subscribe((res) => {
      if (res) {
        this.setInternal({ tokenExpire: false });
        this.fetchFollowArtists();
        this.fetchMyPlaylist({
          limit: 50,
        });
      }
    });
  }

  ngOnDestroy() {
    this.userReq$.unsubscribe();
  }
}
