import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';
import { MyPlaylistActionsType } from './actions.type';
import { SpotifyModel } from '../index';

import { AuthService } from '../../auth/auth.service';

@State<SpotifyModel>({
  name: 'my_playlist',
  defaults: null,
})
@Injectable()
export class MyPlaylistState {
  constructor(private service: MyPlaylistService, private auth: AuthService) {}

  @Action(MyPlaylistActionsType.FetchPlaylist)
  fetchPlaylist(
    { getState, patchState }: StateContext<SpotifyModel>,
    { queryParams }: MyPlaylistActionsType.FetchPlaylist
  ) {
    const state$ = getState();

    if (state$ == null) {
      return this.service.fetchPlaylist(queryParams).pipe(
        map((result: any) => {
          patchState(result);
        }),
        catchError((error: any) => {
          console.log('top-tracks', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
