import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { SubSink } from 'subsink';
import { Toast } from '../../services/index';

// import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';

import { PlaylistPreviewActionsType } from '../../store/playlist-preview/actions.type';
import { PlaylistPreviewSelectors } from '../../store/playlist-preview/selectors';
import { PlaylistPreviewModel } from '../../store/playlist-preview/model';

import { RecommendationActionsType } from '../../store/recommendation/actions.type';
import { RecommendationSelectors } from '../../store/recommendation/selectors';
import { RecommendationModel } from '../../store/recommendation/model';
import { QueryParams } from '../../store/index';

import { CurrentUserSelectors } from '../../store/current-user/selectors';
import { CurrentUserModel } from '../../store/current-user/model';

@Component({
  selector: 'app-recommendation',
  templateUrl: './recommendation.component.html',
  styleUrls: ['./recommendation.component.scss'],
})
export class RecommendationComponent implements OnInit, OnDestroy {
  @Dispatch() playlistPreview = (playlistId: string) =>
    new PlaylistPreviewActionsType.FetchPlaylistPreview(playlistId);

  @Dispatch() recommendation = (params: QueryParams) =>
    new RecommendationActionsType.FetchRecommendation(params);

  @Select(PlaylistPreviewSelectors.fetchPlaylistPreview)
  playlist$: Observable<PlaylistPreviewModel>;

  @Select(RecommendationSelectors.fetchRecommendation)
  recommendation$: Observable<RecommendationModel>;

  @Select(CurrentUserSelectors.fetchCurrentUser)
  currentUser$: Observable<CurrentUserModel>;

  req$ = new SubSink();
  toast = Toast();
  playlist: PlaylistPreviewModel;

  constructor(
    private activeRoute: ActivatedRoute // private http: MyPlaylistService
  ) {}
  ngOnInit(): void {
    const playlistId = this.activeRoute.snapshot.params.playlistId;
    this.playlistPreview(playlistId);

    this.req$.sink = this.playlist$.subscribe(
      (res) => {
        if (res) {
          this.playlist = res;
          const trackIds = res.tracks.items.slice(0, 5).map((i) => i.track.id);

          this.getRecommendation(trackIds);
        }
      },
      (error) => {
        console.log('playlist is null');
        this.toast.fire({ icon: 'error', title: 'Playlist Preview is null' });
      }
    );
  }

  getRecommendation(trackIds: Array<string>) {
    this.recommendation({
      limit: 20,
      seed_tracks: trackIds.toString(),
    });
  }

  ngOnDestroy() {
    this.req$.unsubscribe();
  }
}
