import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { StateReset } from 'ngxs-reset-plugin';
import { Store } from '@ngxs/store';
import { Router } from '@angular/router';
import { Select } from '@ngxs/store';
import { Observable } from 'rxjs';

import { errors } from './utils';
import { SubSink } from 'subsink';
import { Toast } from '../../services/index';

import { PlaylistPreviewModel } from '../../store/playlist-preview/model';
import { RecommendationModel } from '../../store/recommendation/model';
import { CurrentUserModel } from '../../store/current-user/model';

import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';
import { MyPlaylistActionsType } from '../../store/my-playlist/actions.type';
import { MyPlaylistSelectors } from '../../store/my-playlist/selectors';
import { MyPlaylistState } from '../../store/my-playlist/state';
import { SpotifyModel } from '../../store/index';

@Component({
  selector: 'app-recommendation-form',
  templateUrl: './recommendation-form.component.html',
  styleUrls: ['./recommendation-form.component.scss'],
})
export class RecommendationFormComponent implements OnInit, OnDestroy {
  recomForm: FormGroup;
  inputErrors: any = errors;
  isSubmited: boolean;
  isModalActive: boolean;
  uris: string;

  req$ = new SubSink();
  reqPlaylist$ = new SubSink();
  toast = Toast();
  isRecomCreated: any = JSON.parse(localStorage.getItem('recommendation'));
  isRecomExist: boolean;

  @Input() playlist: PlaylistPreviewModel;
  @Input() currentUser: CurrentUserModel;
  @Input() recommendation: RecommendationModel;

  @Dispatch() fetchMyPlaylist = (qry = null) =>
    new MyPlaylistActionsType.FetchPlaylist(qry);

  @Select(MyPlaylistSelectors.fetchPlaylist) playlist$: Observable<
    SpotifyModel
  >;

  constructor(
    private FB: FormBuilder,
    private http: MyPlaylistService,
    private store: Store,
    private route: Router
  ) {}

  get FC() {
    return this.recomForm.controls;
  }

  ngOnInit(): void {
    this.recomForm = this.FB.group({
      name: [
        `Recommended Tracks Based On  ${this.playlist.name}`,
        [Validators.required, Validators.minLength(3)],
      ],
      description: ['', [Validators.minLength(25)]],
      public: [true],
    });

    this.reqPlaylist$.sink = this.playlist$.subscribe((play) => {
      if (play && this.isRecomCreated) {
        const collect = play.items.filter(
          (item) => item.id === this.isRecomCreated.playlistId
        );
        if (collect.length > 0) {
          this.isRecomExist = true;
        }
      }
    });
  }

  showCreateForm(user: CurrentUserModel, recom: RecommendationModel) {
    if (user && recom) {
      this.uris = recom.tracks.map((track) => track.uri).toString();
      this.isModalActive = true;
    }
  }

  saveToSpotify(userId) {
    this.isSubmited = true;
    if (!this.recomForm.valid) return;

    const form = this.recomForm.value;
    const formObj =
      form.description !== '' ? form : { name: form.name, public: form.public };

    this.req$.sink = this.http.createPlaylist(userId, formObj).subscribe(
      (res) => {
        this.pushCollection(res);
        this.setLocalStorage();
      },
      (err) => {
        this.toast.fire({
          icon: 'error',
          title: 'Failed request save recommendation',
        });
        console.log(err);
      }
    );
  }

  setLocalStorage() {
    localStorage.removeItem('recommendation');
    this.store.dispatch(new StateReset(MyPlaylistState));
    this.isModalActive = false;
    this.isSubmited = false;

    localStorage.setItem(
      'recommendation',
      JSON.stringify({ playlistId: this.playlist.id, isSubmitted: true })
    );

    setTimeout(() => {
      this.isRecomExist = true;
      this.isRecomCreated = JSON.parse(localStorage.getItem('recommendation'));
    }, 1000);
  }

  pushCollection(res: PlaylistPreviewModel) {
    const recomString = this.recommendation.tracks
      .map((track) => track.uri)
      .toString();

    if (res) {
      this.http.pushTracksToPlaylist(res.id, recomString).subscribe(
        (res) => {
          this.fetchMyPlaylist({ limit: 50 });
          this.toast.fire({
            icon: 'success',
            title: 'Playlist has been save to collections',
          });
        },
        (err) => {
          this.toast.fire({
            icon: 'error',
            title: 'Failed push request recommendation items',
          });
          console.log(err);
        }
      );
    }
  }

  openPlaylist() {
    this.route.navigate(['my-playlist']);
  }

  ngOnDestroy() {
    this.req$.unsubscribe();
    this.reqPlaylist$.unsubscribe();
  }
}
