import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { RecommendationActionsType } from './actions.type';
import { RecommendationModel } from './model';
import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';

@State<RecommendationModel>({
  name: 'recommendation',
  defaults: null,
})
@Injectable()
export class RecommendationState {
  constructor(private service: MyPlaylistService) {}

  @Action(RecommendationActionsType.FetchRecommendation)
  fetchRecommendation(
    { getState, patchState }: StateContext<RecommendationModel>,
    { queryParams }: RecommendationActionsType.FetchRecommendation
  ) {
    const state$ = getState();

    if (state$ == null) {
      return this.service.getRecommendation(queryParams).pipe(
        map((result: any) => {
          patchState(result);
        }),
        catchError((error: any) => {
          console.log('get-recommendation', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
