import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { TopArtistsService } from '../../services/top-artists/top-artists.service';
import { TopArtistsActionsType } from './actions.type';
import { SpotifyModel } from '../index';

import { AuthService } from '../../auth/auth.service';

@State<SpotifyModel>({
  name: 'top_artists',
  defaults: null,
})
@Injectable()
export class TopArtistsState {
  constructor(private service: TopArtistsService, private auth: AuthService) {}

  @Action(TopArtistsActionsType.FetchTopArtists)
  fetchTopArtists(
    { getState, patchState }: StateContext<SpotifyModel>,
    { queryParams }: TopArtistsActionsType.FetchTopArtists
  ) {
    const state$ = getState();
    const tp = JSON.parse(localStorage.getItem('top-artists'));

    if (
      state$ == null ||
      tp.time_range === 'undefined' ||
      tp.time_range !== queryParams?.time_range
    ) {
      return this.service.fetchTopArtists(queryParams).pipe(
        map((result: any) => {
          patchState(result);
          localStorage.setItem(
            'top-artists',
            JSON.stringify({ time_range: queryParams?.time_range })
          );
        }),
        catchError((error: any) => {
          console.log('top-tracks', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
