import {
  Component,
  OnInit,
  EventEmitter,
  Output,
  OnDestroy,
} from '@angular/core';
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { SubSink } from 'subsink';

import { CurrentUserActionsType } from '../../store/current-user/actions.type';
import { MyPlaylistActionsType } from '../../store/my-playlist/actions.type';
import { FollowActionsType } from '../../store/follow/actions.type';

import { InternalActionsType } from '../../store/internal/actions.type';
import { InternalSelectors } from '../../store/internal/selectors';
import { InternalModel } from '../../store/internal/model';
import { AuthService } from '../../auth/auth.service';

@Component({
  selector: 'app-token-expiration-modal',
  templateUrl: './token-expiration-modal.component.html',
  styleUrls: ['./token-expiration-modal.component.scss'],
})
export class TokenExpirationModalComponent implements OnInit, OnDestroy {
  reqAuth$ = new SubSink();
  faWarning = faExclamationTriangle;

  @Output() onRefreshToken = new EventEmitter();
  @Select(InternalSelectors.fetchInternal) static$: Observable<InternalModel>;

  @Dispatch() setInternal = (params) =>
    new InternalActionsType.setInternal(params);

  @Dispatch() fetchCurrentUser = () =>
    new CurrentUserActionsType.FetchCurrentUser();

  @Dispatch() fetchMyPlaylist = (qry) =>
    new MyPlaylistActionsType.FetchPlaylist(qry);

  @Dispatch() fetchFollowArtists = () =>
    new FollowActionsType.FetchFollowArtists();

  constructor(private auth: AuthService, private store: Store) {}

  ngOnInit(): void {}

  emitPageAction() {
    const { current_user } = this.store.snapshot();

    this.reqAuth$.sink = this.auth.refreshToken().subscribe((res) => {
      localStorage.removeItem('access_token');
      localStorage.setItem('access_token', res.access_token);

      this.setInternal({ tokenExpire: false });
      this.onRefreshToken.emit();

      if (!current_user) {
        this.fetchCurrentUser();
        this.fetchFollowArtists();
        this.fetchMyPlaylist({
          limit: 50,
        });
      }
    });
  }

  ngOnDestroy() {
    this.reqAuth$.unsubscribe();
  }
}
