import { Component, OnInit, OnDestroy, Input } from '@angular/core';

import { ArtistPreviewModel } from '../../store/artist-preview/model';
import { FollowArtistItemModel } from '../../store/follow/artists-model';

import { FollowActionsType } from '../../store/follow/actions.type';
import { FollowService } from '../../services/follow/follow.service';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { SubSink } from 'subsink';

// import Swal from 'sweetalert2';
import { Toast, SwalFire } from '../../services/index';

@Component({
  selector: 'app-follow-btn',
  templateUrl: './follow-btn.component.html',
  styleUrls: ['./follow-btn.component.scss'],
})
export class FollowBtnComponent implements OnInit, OnDestroy {
  constructor(private follow: FollowService) {}

  @Dispatch() fetchFollowArtists = () =>
    new FollowActionsType.FetchFollowArtists();

  @Input() artist: ArtistPreviewModel;
  @Input() following: FollowArtistItemModel[];

  Toast = Toast();
  swalFire = SwalFire;
  putRequest$ = new SubSink();
  $isFollowed = false;
  $unfollowHover = false;

  ngOnDestroy() {
    this.putRequest$.unsubscribe();
  }

  ngOnInit(): void {
    const fil = this.following.filter((follow) => follow.id === this.artist.id);
    this.$isFollowed = fil.length > 0 ? true : false;
    // console.log('this.$isFollowed', this.$isFollowed);
  }

  toastCall(stat, msg) {
    this.Toast.fire({ icon: stat == 1 ? 'success' : 'error', title: msg });
  }

  // swalFire(callback, options) {
  //   Swal.fire({
  //     ...options,
  //     imageWidth: 200,
  //     imageHeight: 200,
  //     confirmButtonColor: '#1db954',
  //     cancelButtonColor: '#8e9396',
  //     showCancelButton: true,
  //   }).then(callback);
  // }

  followArtist(artist) {
    const options = {
      title: artist.name,
      imageUrl: artist.images[0].url,
      confirmButtonText: 'Confirm Follow',
    };

    this.swalFire((result) => {
      if (result.value) {
        this.putRequest$.sink = this.follow.followArtist(artist.id).subscribe(
          (res) => {
            this.toastCall(1, 'Artist has been followed');
            this.fetchFollowArtists();
            this.$isFollowed = true;
          },
          (err) => this.toastCall(2, 'Follow request failed!')
        );
      }
    }, options);
  }

  unFollowArtist(artist) {
    const options = {
      title: artist.name,
      text: 'Are you sure you want to continue?',
      imageUrl: artist.images[0].url,
      confirmButtonText: 'Unfollow Artist',
    };

    this.swalFire((result) => {
      if (result.value) {
        this.putRequest$.sink = this.follow.unFollowArtist(artist.id).subscribe(
          (res) => {
            this.toastCall(1, 'Artist has been unfollowed');
            this.fetchFollowArtists();
            this.$isFollowed = false;
          },
          (err) => this.toastCall(2, 'Follow request failed!')
        );
      }
    }, options);
  }
}
