import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { RelatedArtistsService } from '../../services/related-artists/related-artists.service';
import { RelatedArtistActionsType } from './actions.type';
import { RelatedArtistsModel } from './model';

import { AuthService } from '../../auth/auth.service';

@State<RelatedArtistsModel>({
  name: 'related_artists',
  defaults: null,
})
@Injectable()
export class RelatedArtistsState {
  constructor(
    private service: RelatedArtistsService,
    private auth: AuthService
  ) {}

  @Action(RelatedArtistActionsType.FetchRelatedArtist)
  fetchRelatedArtists(
    { getState, patchState }: StateContext<RelatedArtistsModel>,
    { artistId }: RelatedArtistActionsType.FetchRelatedArtist
  ) {
    return this.service.fetchRelatedArtists(artistId).pipe(
      map((result: any) => {
        patchState(result);
      }),
      catchError((error: any) => {
        console.log('top-tracks', error);
        // this.auth.authenticateUser();
        return throwError(error);
      })
    );
  }
}
