import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError, finalize } from 'rxjs/operators';
import { AuthService } from '../auth/auth.service';
// import { SubSink } from 'subsink';
import { interSect } from './utils';

import { Toast } from '../services';
// import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { Store } from '@ngxs/store';
import { StateOverwrite } from 'ngxs-reset-plugin';
import { InternalState } from '../store/internal/state';
import { Router } from '@angular/router';

@Injectable()
export class ErrorHandlerInterceptor implements HttpInterceptor {
  toast = Toast();
  // reqAuth$ = new SubSink();

  constructor(
    private auth: AuthService,
    private store: Store,
    private route: Router
  ) {}

  intercept(
    request: HttpRequest<unknown>,
    next: HttpHandler
  ): Observable<HttpEvent<unknown>> {
    // check if https://accounts.spotify.com/api/token request
    const intersect = interSect(request.url, ['token']);
    if (intersect.length > 0) {
      return next.handle(request).pipe(
        retry(1),
        catchError((err) => {
          this.route.navigate(['login']);
          return throwError(err);
        })
      );
    }

    return next.handle(request).pipe(
      // retry on failure
      retry(1),
      // Handle Error
      catchError((err) => {
        this.toast.fire({
          icon: 'error',
          title: 'Token Expire',
          text: 'generate refresh token',
        });

        this.store.dispatch(
          new StateOverwrite([InternalState, { tokenExpire: true }])
        );
        return throwError(err);
      }),
      finalize(() => {
        // this.refreshToken();
      })
    );
  }

  // refreshToken() {
  //   this.reqAuth$.sink = this.auth.refreshToken().subscribe((res) => {
  //     localStorage.removeItem('access_token');
  //     localStorage.setItem('access_token', res.access_token);
  //   });
  // }

  // ngOnDestroy() {
  //   this.reqAuth$.unsubscribe();
  // }
}
