import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { ArtistPreviewService } from '../../services/artist-preview/artist-preview.service';
import { ArtistPreviewActionsType } from './actions.type';
import { ArtistPreviewModel } from './model';

import { AuthService } from '../../auth/auth.service';

@State<ArtistPreviewModel>({
  name: 'artist_preview',
  defaults: null,
})
@Injectable()
export class ArtistPreviewState {
  constructor(
    private service: ArtistPreviewService,
    private auth: AuthService
  ) {}

  @Action(ArtistPreviewActionsType.FetchArtistPreview)
  fetchArtistPreview(
    { getState, patchState }: StateContext<ArtistPreviewModel>,
    { artistId }: ArtistPreviewActionsType.FetchArtistPreview
  ) {
    return this.service.fetchArtistPreview(artistId).pipe(
      map((result: any) => {
        patchState(result);
      }),
      catchError((error: any) => {
        console.log('top-tracks', error);
        // this.auth.authenticateUser();
        return throwError(error);
      })
    );
  }
}
