import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { TopTracksService } from '../../services/top-tracks/top-tracks.service';
import { TopTracksActionsType } from './actions.type';
import { SpotifyModel } from '../index';

import { AuthService } from '../../auth/auth.service';

@State<SpotifyModel>({
  name: 'top_tracks',
  defaults: null,
})
@Injectable()
export class TopTracksState {
  constructor(private service: TopTracksService, private auth: AuthService) {}

  @Action(TopTracksActionsType.FetchTopTracks)
  fetchTopTracks(
    { getState, patchState }: StateContext<SpotifyModel>,
    { queryParams }: TopTracksActionsType.FetchTopTracks
  ) {
    const state$ = getState();
    const tp = JSON.parse(localStorage.getItem('top-tracks'));

    if (
      state$ == null ||
      tp.time_range === 'undefined' ||
      tp.time_range !== queryParams?.time_range
    ) {
      return this.service.fetchTopTracks(queryParams).pipe(
        map((result: any) => {
          patchState(result);
          localStorage.setItem(
            'top-tracks',
            JSON.stringify({ time_range: queryParams?.time_range })
          );
        }),
        catchError((error: any) => {
          console.log('top-tracks', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
