import { Component, OnInit, Input } from '@angular/core';

import { faEye } from '@fortawesome/free-solid-svg-icons';
import { TopArtistsModel } from '../../store/top-artists/model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-grid-playlist',
  templateUrl: './grid-playlist.component.html',
  styleUrls: ['./grid-playlist.component.scss'],
})
export class GridPlaylistComponent implements OnInit {
  faEye = faEye;
  @Input() artists: TopArtistsModel[];

  defaultImage = 'https://www.placecage.com/1000/1000';
  image = 'https://images.unsplash.com/photo-1443890923422-7819ed4101c0?fm=jpg';

  constructor(private route: Router) {}

  ngOnInit(): void {}

  withComma(n) {
    return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  }

  previewArtist(artist) {
    this.route.navigate(['artist', artist.id]);
  }
}
