import { Component, OnInit, Input } from '@angular/core';
import { faEllipsisV, faPlay } from '@fortawesome/free-solid-svg-icons';

import { TopTrackItemModel } from '../../store/top-tracks/model';
import { msToTime } from '../../services';
import { Router } from '@angular/router';

@Component({
  selector: 'app-track-item',
  templateUrl: './track-item.component.html',
  styleUrls: ['./track-item.component.scss'],
})
export class TrackItemComponent implements OnInit {
  faEllipsisV = faEllipsisV;
  faPlay = faPlay;
  msToTime = msToTime;

  @Input() track: TopTrackItemModel;

  constructor(private route: Router) {}

  ngOnInit(): void {}

  routeToAudioFeature(track) {
    localStorage.removeItem('selected-audio-feature');
    localStorage.setItem(
      'selected-audio-feature',
      JSON.stringify({
        image: track.album.images[0].url,
        name: track.name,
        artists: track.artists,
        description: track.album.name,
        popularity: track.popularity,
        id: track.id,
        external_urls: track.external_urls,
      })
    );

    this.route.navigate(['audio-feature', track.id]);
  }
}
