import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { environment } from 'src/environments/environment';
import { httpOptions, setQueryParams } from '../index';
import { QueryParams } from '../../store';

@Injectable({
  providedIn: 'root',
})
export class TopArtistsService {
  constructor(private http: HttpClient) {}

  fetchTopArtists(queryParams: QueryParams = null) {
    const $queryString = setQueryParams(queryParams);

    return this.http.get<any[]>(
      `${environment.spotify_api}me/top/artists?${$queryString}`
      // { ...httpOptions }
    );
  }
}
