import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import Axios from 'axios-observable';

import { environment } from 'src/environments/environment';
import { httpOptions, setQueryParams } from '../index';
import { QueryParams } from '../../store';

@Injectable({
  providedIn: 'root',
})
export class MyPlaylistService {
  axios = Axios;

  constructor(private http: HttpClient) {}

  fetchPlaylist(queryParams: QueryParams = null) {
    const $queryString = setQueryParams(queryParams);

    return this.http.get<any>(
      `${environment.spotify_api}me/playlists?${$queryString}`
      // { ...httpOptions }
    );
  }

  fetchPreviewPlaylist(playlistId: string) {
    return this.http.get<any>(
      `${environment.spotify_api}playlists/${playlistId}`
      // { ...httpOptions }
    );
  }

  editPlaylistDetails(playlistId: string, reqBody: any) {
    return this.http.put<any>(
      `${environment.spotify_api}playlists/${playlistId}`,
      reqBody
      // { ...httpOptions }
    );
  }

  getRecommendation(queryParams: QueryParams) {
    const $queryString = setQueryParams(queryParams);

    return this.http.get<any>(
      `${environment.spotify_api}recommendations?${$queryString}`
      // { ...httpOptions }
    );
  }

  createPlaylist(userId: string, params: any) {
    return this.http.post<any>(
      `${environment.spotify_api}users/${userId}/playlists`,
      params
      // { ...httpOptions }
    );
  }

  pushTracksToPlaylist(playlistId: string, uris: string) {
    return this.http.post<any>(
      `${environment.spotify_api}playlists/${playlistId}/tracks?uris=${uris}`,
      {}
      // { ...httpOptions }
    );
  }

  removePlaylistItem(playlistId: string, tracks: any) {
    return this.axios.delete(
      `${environment.spotify_api}playlists/${playlistId}/tracks`,
      {
        ...httpOptions,
        data: tracks,
      }
    );
  }
}
