import { Component, OnInit, Input } from '@angular/core';
import { SubSink } from 'subsink';
// import Swal from 'sweetalert2';

import { FollowService } from '../../services/follow/follow.service';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { StateReset } from 'ngxs-reset-plugin';
import { Toast, SwalFire } from '../../services/index';
import { Store } from '@ngxs/store';

import { faTrash, faTrashRestoreAlt } from '@fortawesome/free-solid-svg-icons';

import { MyPlaylistActionsType } from '../../store/my-playlist/actions.type';
import { MyPlaylistState } from '../../store/my-playlist/state';

import { MyPlaylistItemModel } from '../../store/my-playlist/model';
import { CurrentUserModel } from '../../store/current-user/model';
import { PlaylistPreviewModel } from '../../store/playlist-preview/model';

@Component({
  selector: 'app-follow-btn-playlist',
  templateUrl: './follow-btn-playlist.component.html',
  styleUrls: ['./follow-btn-playlist.component.scss'],
})
export class FollowBtnPlaylistComponent implements OnInit {
  constructor(private follow: FollowService, private store: Store) {}

  @Dispatch() fetchMyPlaylist = (qry = null) =>
    new MyPlaylistActionsType.FetchPlaylist(qry);

  @Input() playlist: PlaylistPreviewModel;
  @Input() playlistCollection: MyPlaylistItemModel[];
  @Input() currentUser: CurrentUserModel;
  @Input() isMyPlaylist: boolean;

  Toast = Toast();
  swalFire = SwalFire;
  // Swal = Swal;

  faTrash = faTrash;
  faTrashRestoreAlt = faTrashRestoreAlt;

  putRequest$ = new SubSink();
  ifFollowText = 'FOLLOWING';
  isFollowed: boolean;

  ngOnInit(): void {
    if (this.playlistCollection && this.currentUser) {
      const filter = this.playlistCollection.filter(
        (res) => res.id === this.playlist.id
      );
      if (filter.length > 0) this.isFollowed = true;
    }
  }

  toastCall(stat, msg) {
    this.Toast.fire({ icon: stat == 1 ? 'success' : 'error', title: msg });
  }

  // swalFire(callback, options) {
  //   Swal.fire({
  //     ...options,
  //     imageWidth: 200,
  //     imageHeight: 200,
  //     confirmButtonColor: '#1db954',
  //     cancelButtonColor: '#8e9396',
  //     showCancelButton: true,
  //   }).then(callback);
  // }

  unFollowPlaylist(playlist) {
    const options = {
      title: playlist.name,
      text: 'Are you sure to unfollow playlist',
      imageUrl:
        playlist.images.length > 0
          ? playlist.images[0].url
          : 'assets/images/spotify.png',
      confirmButtonText: 'Unfollow Playlist',
    };

    this.swalFire((result) => {
      if (result.value) {
        this.putRequest$.sink = this.follow
          .unFollowPlaylist(playlist.id)
          .subscribe(
            (res) => {
              this.toastCall(1, 'Playlist has been unfollowed');
              this.isFollowed = false;
              this.store.dispatch(new StateReset(MyPlaylistState));
              this.fetchMyPlaylist({ limit: 50 });
            },
            (err) => this.toastCall(2, 'unfollow request failed!')
          );
      }
    }, options);
  }

  followPlaylist(playlist) {
    const options = {
      title: playlist.name,
      input: 'radio',
      inputOptions: { '0': 'Public', '1': 'Private' },
      imageUrl:
        playlist.images.length > 0
          ? playlist.images[0].url
          : 'assets/images/spotify.png',
      confirmButtonText: 'Follow Playlist',
    };

    this.swalFire((result) => {
      if (result.value) {
        this.putRequest$.sink = this.follow
          .followPlaylist(playlist.id)
          .subscribe(
            (res) => {
              this.toastCall(1, 'Playlist has been followed');
              this.isFollowed = true;
              this.store.dispatch(new StateReset(MyPlaylistState));
              this.fetchMyPlaylist({ limit: 50 });
            },
            (err) => this.toastCall(2, 'Follow request failed!')
          );
      }
    }, options);
  }

  unFollowUserPlaylist(playlist) {
    const options = {
      title: playlist.name,
      text:
        'Are you sure!! you lost all tracks from this playlist once you leave this page.',
      imageUrl:
        playlist.images.length > 0
          ? playlist.images[0].url
          : 'assets/images/spotify.png',
      confirmButtonText: 'Delete Playlist',
    };

    this.swalFire((result) => {
      if (result.value && this.isMyPlaylist) {
        this.putRequest$.sink = this.follow
          .unFollowUserPlaylist(playlist.owner.id, playlist.id)
          .subscribe(
            (res) => {
              this.toastCall(1, 'Playlist has been delete');
              this.isFollowed = false;
              this.store.dispatch(new StateReset(MyPlaylistState));
              this.fetchMyPlaylist({ limit: 50 });
            },
            (err) => this.toastCall(2, 'Unfollow request failed!')
          );
      }
    }, options);
  }

  followUserPlaylist(playlist) {
    const options = {
      title: playlist.name,
      input: 'radio',
      inputOptions: { '0': 'Public', '1': 'Private' },
      imageUrl:
        playlist.images.length > 0
          ? playlist.images[0].url
          : 'assets/images/spotify.png',
      confirmButtonText: 'Restore PLaylist',
    };

    this.swalFire((result) => {
      if (result.value && this.isMyPlaylist) {
        const isPublic = result.value == '0' ? true : false;
        this.putRequest$.sink = this.follow
          .followUserPlaylist(playlist.owner.id, playlist.id, isPublic)
          .subscribe(
            (res) => {
              this.toastCall(1, 'Playlist has been Restored');
              this.isFollowed = true;
              this.store.dispatch(new StateReset(MyPlaylistState));
              this.fetchMyPlaylist({ limit: 50 });
            },
            (err) => this.toastCall(2, 'Unfollow request failed!')
          );
      }
    }, options);
  }
}
