import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { faPen } from '@fortawesome/free-solid-svg-icons';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { PlaylistPreviewModel } from '../../store/playlist-preview/model';
import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';
import { errors } from './utils';
import { SubSink } from 'subsink';
import { Toast } from '../../services/index';

import { Dispatch } from '@ngxs-labs/dispatch-decorator';
import { PlaylistPreviewActionsType } from '../../store/playlist-preview/actions.type';

@Component({
  selector: 'app-edit-playlist-btn',
  templateUrl: './edit-playlist-btn.component.html',
  styleUrls: ['./edit-playlist-btn.component.scss'],
})
export class EditPlaylistBtnComponent implements OnInit, OnDestroy {
  @Input() playlist: PlaylistPreviewModel;
  editForm: FormGroup;
  inputErrors: any = errors;
  req$ = new SubSink();
  toast = Toast();

  faPen = faPen;
  isActive: boolean = false;
  isSubmited: boolean;

  @Dispatch() playlistPreview = (playlistId: string) =>
    new PlaylistPreviewActionsType.FetchPlaylistPreview(playlistId);

  constructor(private FB: FormBuilder, private http: MyPlaylistService) {}

  get FC() {
    return this.editForm.controls;
  }

  ngOnInit(): void {
    this.editForm = this.FB.group({
      name: [
        this.playlist.name,
        [Validators.required, Validators.minLength(3)],
      ],
      description: [this.playlist.description, [Validators.minLength(25)]],
      public: [true],
    });
  }

  editPlaylist() {
    this.isActive = true;
  }

  savePlaylist(playlist: PlaylistPreviewModel) {
    this.isSubmited = true;
    if (!this.editForm.valid) return;
    const form = this.editForm.value;
    const dataObj =
      form.description !== '' ? form : { name: form.name, public: form.public };

    this.req$.sink = this.http
      .editPlaylistDetails(playlist.id, dataObj)
      .subscribe(
        (res) => {
          this.toast.fire({
            icon: 'success',
            title: 'Success request edit playlist details',
          });
          this.playlistPreview(playlist.id);
        },
        (err) =>
          this.toast.fire({
            icon: 'error',
            title: 'Failed request edit playlist details',
          })
      );

    this.isActive = false;
  }

  ngOnDestroy() {
    this.req$.unsubscribe();
  }
}
