import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';

import { AudioFeatureActionsType } from '../../store/audio-feature/actions.type';
import { AudioFeatureSelectors } from '../../store/audio-feature/selectors';
import {
  AudioFeatureModel,
  SelectedTrackModel,
} from '../../store/audio-feature/model';

import { msToTime } from '../../services';

@Component({
  selector: 'app-audio-feature',
  templateUrl: './audio-feature.component.html',
  styleUrls: ['./audio-feature.component.scss'],
})
export class AudioFeatureComponent implements OnInit {
  msToTime = msToTime;

  @Dispatch() fetchAudioFeature = (track: SelectedTrackModel) =>
    new AudioFeatureActionsType.FetchAudioFeature(track);

  @Select(AudioFeatureSelectors.fetchAudioFeature) track$: Observable<
    AudioFeatureModel
  >;

  constructor(private route: ActivatedRoute) {}

  ngOnInit(): void {
    const track = JSON.parse(localStorage.getItem('selected-audio-feature'));
    this.fetchAudioFeature(track);

    // this.track$.subscribe((res: AudioFeatureModel) => {
    //   console.log('res', res.image);
    // });
  }

  pitchClass(key: any) {
    switch (key) {
      case 1:
        return 'C, D (also B)';
      case 2:
        return 'D (also C, E)';
      case 3:
        return 'D, E (also F)';
      case 4:
        return 'E (also D, F)';
      case 5:
        return 'F (also E, G)';
      case 6:
        return 'F, G (also E)';
      case 7:
        return 'G (also F, A)';
      case 8:
        return 'G, A';
      case 9:
        return 'A (also G, B)';
      case 10 || 't' || 'A':
        return 'A, B (also C)';
      case 11 || 'e' || 'B':
        return 'B (also A, C)';
      default:
        return 'C (also B, D)';
    }
  }

  findModality(mode) {
    return mode === 1 ? 'Major' : 'minor';
  }

  findBeats(f) {
    if (f) {
      let min = parseFloat((f.duration_ms / 1000 / 60).toFixed(1));
      let temp = parseInt(f.tempo.toFixed());
      return Math.floor(min * temp);
    }
  }

  findBars(f) {
    return Math.floor(this.findBeats(f) / f?.time_signature);
  }
}
