import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { environment } from 'src/environments/environment';
import { httpOptions, setQueryParams } from '../index';
import { QueryParams } from '../../store';

@Injectable({
  providedIn: 'root',
})
export class FollowService {
  constructor(private http: HttpClient) {}

  fetchFollowArtists(queryParams: QueryParams = null) {
    const $queryString = setQueryParams(queryParams);

    return this.http.get<any[]>(
      `${environment.spotify_api}me/following?type=artist&${$queryString}`
      // { ...httpOptions }
    );
  }

  fetchFollowTracks(queryParams: QueryParams = null) {
    const $queryString = setQueryParams(queryParams);

    return this.http.get<any[]>(
      `${environment.spotify_api}me/following?type=tracks&${$queryString}`
      // { ...httpOptions }
    );
  }

  followArtist(artistId: string) {
    return this.http.put<any>(
      `${environment.spotify_api}me/following?type=artist&ids=${artistId}`,
      {}
      // { ...httpOptions }
    );
  }

  unFollowArtist(artistId: string) {
    return this.http.delete<any>(
      `${environment.spotify_api}me/following?type=artist&ids=${artistId}`
      // { ...httpOptions }
    );
  }
  // default
  followPlaylist(playlistId: string) {
    return this.http.put<any>(
      `${environment.spotify_api}playlists/${playlistId}/followers`,
      {}
      // { ...httpOptions }
    );
  }

  unFollowPlaylist(playlistId: string) {
    return this.http.delete<any>(
      `${environment.spotify_api}playlists/${playlistId}/followers`
      // { ...httpOptions }
    );
  }

  // active user
  unFollowUserPlaylist(userId: string, playlistId: string) {
    return this.http.delete<any>(
      `${environment.spotify_api}users/${userId}/playlists/${playlistId}/followers`
      // { ...httpOptions }
    );
  }

  followUserPlaylist(
    userId: string,
    playlistId: string,
    isPublic: boolean = false
  ) {
    return this.http.put<any>(
      `${environment.spotify_api}users/${userId}/playlists/${playlistId}/followers`,
      { publid: isPublic }
      // { ...httpOptions }
    );
  }
}
