import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// import { MainComponent } from './main.component';
// import { IndexComponent } from '../index/index.component';
import { TopTracksComponent } from '../top-tracks/top-tracks.component';
import { AudioFeatureComponent } from '../audio-feature/audio-feature.component';
import { TopArtistsComponent } from '../top-artists/top-artists.component';
import { ArtistPreviewComponent } from '../artist-preview/artist-preview.component';
import { MyPlaylistComponent } from '../my-playlist/my-playlist.component';
import { PlaylistPreviewComponent } from '../playlist-preview/playlist-preview.component';
import { RecommendationComponent } from '../recommendation/recommendation.component';

const routes: Routes = [
  // { path: '', component: TopTracksComponent },
  { path: '', component: MyPlaylistComponent },
  { path: 'my-playlist/:playlistId', component: PlaylistPreviewComponent },
  { path: 'top-tracks', component: TopTracksComponent },
  { path: 'audio-feature/:trackId', component: AudioFeatureComponent },
  { path: 'top-artists', component: TopArtistsComponent },
  { path: 'artist/:artistId', component: ArtistPreviewComponent },
  {
    path: 'my-playlist/:playlistId/recommendation',
    component: RecommendationComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MainRoutingModule {}
