import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { ArtistTopTracksService } from '../../services/artist-top-tracks/artist-top-tracks.service';
import { ArtistTopTracksActionsType } from './actions.type';
import { ArtistTopTracksModel } from './model';

import { AuthService } from '../../auth/auth.service';

@State<ArtistTopTracksModel>({
  name: 'artist_top_tracks',
  defaults: null,
})
@Injectable()
export class ArtistTopTracksState {
  constructor(
    private service: ArtistTopTracksService,
    private auth: AuthService
  ) {}

  @Action(ArtistTopTracksActionsType.FetchArtistTopTracks)
  fetchTopTracks(
    { getState, patchState }: StateContext<ArtistTopTracksModel>,
    { artistId }: ArtistTopTracksActionsType.FetchArtistTopTracks
  ) {
    return this.service.fetchTopTracks(artistId).pipe(
      map((result: any) => {
        patchState(result);
      }),
      catchError((error: any) => {
        console.log('top-tracks', error);
        // this.auth.authenticateUser();
        return throwError(error);
      })
    );
  }
}
