import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { MyPlaylistService } from '../../services/my-playlist/my-playlist.service';
import { PlaylistPreviewActionsType } from './actions.type';
import { PlaylistPreviewModel } from './model';

import { AuthService } from '../../auth/auth.service';

@State<PlaylistPreviewModel>({
  name: 'playlist_preview',
  defaults: null,
})
@Injectable()
export class PlaylistPreviewState {
  constructor(private service: MyPlaylistService, private auth: AuthService) {}

  @Action(PlaylistPreviewActionsType.FetchPlaylistPreview)
  fetchPlaylistPreview(
    { getState, patchState }: StateContext<PlaylistPreviewModel>,
    { playlistId }: PlaylistPreviewActionsType.FetchPlaylistPreview
  ) {
    const state$ = getState();

    if (state$ == null) {
      return this.service.fetchPreviewPlaylist(playlistId).pipe(
        map((result: any) => {
          patchState(result);
        }),
        catchError((error: any) => {
          console.log('playlist-preview', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
