import { Injectable } from '@angular/core';
import { State, StateContext, Action } from '@ngxs/store';

import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { AuthService } from '../../auth/auth.service';
import { CurrentUserActionsType } from './actions.type';
import { CurrentUserModel } from './model';

@State<CurrentUserModel>({
  name: 'current_user',
  defaults: null,
})
@Injectable()
export class CurrentUserState {
  constructor(private auth: AuthService) {}

  @Action(CurrentUserActionsType.FetchCurrentUser)
  fetchCurrentUser({ getState, patchState }: StateContext<CurrentUserModel>) {
    const state$ = getState();

    if (state$ == null) {
      return this.auth.fetchCurrentUser().pipe(
        map((result: any) => {
          localStorage.setItem('country', result.country);
          patchState(result);
        }),
        catchError((error: any) => {
          console.log('current-user', error);
          // this.auth.authenticateUser();
          return throwError(error);
        })
      );
    }
  }
}
