import { Component, OnInit, Query } from '@angular/core';

import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { Dispatch } from '@ngxs-labs/dispatch-decorator';

import { TopTracksActionsType } from '../../store/top-tracks/actions.type';
import { TopTracksSelectors } from '../../store/top-tracks/selectors';

import { SpotifyModel } from '../../store/';
import { tabs } from './top-tracks-util';

@Component({
  selector: 'app-top-tracks',
  templateUrl: './top-tracks.component.html',
  styleUrls: ['./top-tracks.component.scss'],
})
export class TopTracksComponent implements OnInit {
  $tabs = tabs;

  constructor(private store: Store) {}

  @Dispatch() fetchTopTracks = (qry = null) =>
    new TopTracksActionsType.FetchTopTracks(qry);

  @Select(TopTracksSelectors.fetchTopTracks) topTracks$: Observable<
    SpotifyModel
  >;

  ngOnInit(): void {
    const { top_tracks } = this.store.snapshot();

    if (top_tracks === null) {
      const local = JSON.parse(localStorage.getItem('top-tracks'));
      if (!local || local.time_range === 'undefined') {
        this.fetchTopTracks({ limit: 20, time_range: 'long_term' });
        this.setActiveTab('long_term');
      } else {
        this.fetchTopTracks({ limit: 20, time_range: local.time_range });
        this.setActiveTab(local.time_range);
      }
    }
  }

  timeRange(time_range) {
    this.fetchTopTracks({ limit: 20, time_range });
    this.setActiveTab(time_range);
  }

  setActiveTab(time_range: string) {
    this.$tabs.map((tab) => {
      tab.status = tab.timeRange === time_range ? 'is-active' : '';
      return tab;
    });
  }
}
